<template>
    <div>
        <h3>상세 화면</h3>
        글번호 {{row._id}} <br />
        제목   {{row.title}} <br />
        내용   {{row.content}} <br />
        작성자{{row.writer}} <br />
        조회수{{row.hit}} <br />
        이미지 <img :src="`/board/image?_id=${row._id}`" style="width:50px;height:50px" /> <br />
        날짜   {{row.regdate}} <br />
        
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        data(){
            return{
                row : ''
            }
        },
        async created(){
            const url = '/board/content?_id=' + this.$route.query._id;
            const response = await axios.get(url);
            console.log(response);
            if(response.data.ret ===1){
                this.row =response.data.data;
            }
        }
    }   
</script>

