<template>
    <div>
         <el-table :data="rows" style="width: 100%">
            <el-table-column prop="_id" label="글번호" width="180"> </el-table-column>
            <el-table-column label="제목" width="120">
                <template #default="scope">
                    <router-link :to="`/menu3?_id=${scope.row._id}`">{{scope.row.title}}</router-link>
                </template>
            </el-table-column> 
            <el-table-column prop="writer"  label="작성자" width="180"> </el-table-column>
            <el-table-column prop="hit"  label="조회수" width="180"> </el-table-column>
            <el-table-column prop="regdate"  label="날짜" width="180"> </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import axios from 'axios';    
    export default {
        async created() {
            const url = '/board/select?page=1&text=';
            const response = await axios.get(url);
            console.log(response);
            if(response.data.ret === 1) {
                this.rows = response.data.data;
            }
        },
        data(){
            return{
                rows : [ ]
            }
        },
        methods: {
            handleClick(e){
                this.$router.push({
                    path:'/menu3',
                    query:{ _id : this.rows[e]._id}
                });
            }
        }
    }

</script>

